import React from "react"
import { Link } from "react-router-dom";
import logo from "../../assets/images/footer/logo-big.png";
import twitter from "../../assets/images/footer/twitter.svg";
import instagram from "../../assets/images/footer/instagram.svg";
import facebook from "../../assets/images/footer/facebook.svg";
import youtube from "../../assets/images/footer/youtube.svg";
import "./footer.css"



const Footer = () => {

    return (
        <div className="footer">
      <div className="container">
        <div className="footer-grid">
          <div className="footer-column">
            <img src={logo} alt="" />
            <p>CasinoCrypto provides 100% fairness and transparency. To be a user of our site, you must be over 18 years old. If igaming and online betting are not allowed in your region for legal.</p>
          </div>
          <div className="footer-column">
            <h3>Company</h3>
            <ul>
              <li>
                <Link to="/">About Us</Link>
              </li>
              <li>
                <Link to="/">We’re Hiring</Link>
              </li>
              <li>
                <Link to="/">Investors</Link>
              </li>
              <li>
                <Link to="/">Contact</Link>
              </li>
            </ul>
          </div>
          <div className="footer-column">
            <h3>Resources</h3>
            <ul>
              <li>
                <Link to="/">Blog</Link>
              </li>
              <li>
                <Link to="/">Videos</Link>
              </li>
              <li>
                <Link to="/">Case Studies</Link>
              </li>
              <li>
                <Link to="/">Marketplace</Link>
              </li>
            </ul>
          </div>
          <div className="footer-column">
            <h3>Help</h3>
            <ul>
              <li>
                <Link to="/">Support</Link>
              </li>
              <li>
                <Link to="/">Knowledge Base</Link>
              </li>
              <li>
                <Link to="/">Live Chat</Link>
              </li>
            </ul>
          </div>
          <div className="footer-column">
            <h3>Partners</h3>
            <ul>
              <li>
                <Link to="/">Development Partners</Link>
              </li>
              <li>
                <Link to="/">Affiliate Program</Link>
              </li>
              <li>
                <Link to="/">Partner Offers</Link>
              </li>
            </ul>
          </div>
        </div>

        <div className="bottom-footer">
          <ul className="social-icon">
            <li>
              <Link to="/">
                <img src={twitter} alt="" />
              </Link>
            </li>
            <li>
              <Link to="/">
                <img src={instagram} alt="" />
              </Link>
            </li>
            <li>
              <Link to="/">
                <img src={facebook} alt="" />
              </Link>
            </li>
            <li>
              <Link to="/">
                <img src={youtube} alt="" />
              </Link>
            </li>
          </ul>

          <ul className="auth-menu">
            <li>
              <span>© 2021 CasinoCrypto</span>
            </li>
          </ul>
        </div>
      </div>
    </div>
    );
};

export default Footer;