/* eslint-disable react/prop-types */
import React from "react";

import { Table, Modal } from "react-bootstrap";

const Result = ({ show, handleClose, gameResult }) => {
  console.log("gameResult", gameResult);
  return (
    <div className="bet-history">
      <Modal
        show={show}
        onHide={handleClose}
        dialogClassName="history-popup"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title>bet History</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="history-popup-content">
            <Table striped bordered responsive>
              <thead>
                <tr>
                  <th>RoundId</th>
                  <th>Result</th>
                  <th>Bet</th>
                  <th>Multiplier</th>
                  <th>Payout</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{gameResult && gameResult.gameId?.nonce}</td>
                  <td>{gameResult && gameResult.result}</td>
                  <td>{gameResult && gameResult.betAmount}</td>
                  <td>{gameResult && gameResult.multiplier}</td>
                  <td>{gameResult && gameResult.profit_loss}</td>
                </tr>
              </tbody>
            </Table>
          </div>
        </Modal.Body>
      </Modal>
    </div>
  );
};

export default Result;
