import { useEffect } from 'react';
import { getGameHistory, getLastBets } from '../../reducers/gameReducers';
import { useSelector, useDispatch } from 'react-redux';

const useGameHistory = () => {
	const gameObj = useSelector((state) => state.game);
	const gameHistory = gameObj.gamehistory.history;
	const gamesBets = gameObj.gamesBets;
	const userBets = gameObj.userBets;
	const dispatch = useDispatch();
	useEffect(() => {
		if (!gamesBets || !gamesBets.length) {
			dispatch(getLastBets());
		}
		if (!gameHistory || !gameHistory.length) {
			dispatch(getGameHistory('history'));
		}
	}, []);

	return [gameHistory, gamesBets, userBets];
};

export default useGameHistory;
