import { useState } from "react";
import { Table, Button, Tabs, Tab } from "react-bootstrap";
// import PropTypes from 'prop-types';
import { CopyToClipboard } from "react-copy-to-clipboard";
import { toast } from "react-toastify";
import { Link } from "react-router-dom";
import useGameHistory from "./useGameHistory";
import { client } from "../../config/keys";
import Result from "../RouleteeResultPopop/result";
import usernew from '../../assets/images/roulette/user2.jpg'
import money from '../../assets/images/header/money.png';
// curGamePlayerStatus will be a function for all games
// where this function will render 4 status running, draw, win
// and loss can add more if needed and according to the status
// the class will be applied

const BetHistory = () => {
  const [gameHistory, gamesBets, userBets] = useGameHistory();
  const [show, setShow] = useState(false);
  const [gameResult, setGameResult] = useState();

  const copyClipBoard = () => {
    toast.success("Copied to Clipboard", {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      toastId: 1,
    });
  };

  const handleLogin = () => {
    window.location.href = `${client}?login=login`;
  };

  const handleShow = (gamedetails) => {
    setGameResult(gamedetails);
    setShow(true);
  };
  const handleClose = () => setShow(false);

  return (
    <div className="bottom-table">
      <Tabs defaultActiveKey="ALL BETS" id="ALL BETS">
        <Tab eventKey="MY BETS" title="MY BETS">
          <Table bordered responsive variant="dark">
            <thead>
              <tr>
                <th>Game</th>

                <th>Type</th>
                <th>Amount</th>
                <th>Multiplier</th>
                <th>Result</th>
                <th>Payout (BSV)</th>
              </tr>
            </thead>
            <tbody>
              {localStorage.usertoken ? (
                <>
                  {userBets &&
                    userBets.length > 0 &&
                    userBets.map((el, i) => (
                      <tr key={`gamesBetsTr-${Date.now() + i}`}>
                        <td>{el?.gameType ? el.gameType : "---"} </td>
                        {/* <td>
											{el?.userid?.username ? el?.userid?.username : '---'}
										</td> */}
                        <td>{el?.betType ? el.betType : "---"} </td>
                        <td>${el?.betAmount ? el.betAmount : "0"} </td>
                        <td>{el?.multiplier ? `${el.multiplier}X` : "---"} </td>
                        <td> {el?.result ? el.result : "---"} </td>
                        <td
                          className={`${
                            el?.profit_loss
                              ? el.profit_loss > 0
                                ? "text-success"
                                : "text-danger"
                              : ""
                          }`}
                        >
                          ${el?.profit_loss ? el.profit_loss.toFixed(2) : "0"}{" "}
                        </td>
                      </tr>
                    ))}
                </>
              ) : (
                <tr>
                  <td colSpan={6} className="text-align-center">
                    <Button onClick={handleLogin} className="btn-warning">
                      {" "}
                      Login
                    </Button>
                  </td>
                </tr>
              )}
            </tbody>
          </Table>
        </Tab>
        <Tab eventKey="ALL BETS" title="ALL BETS">
          <Table bordered responsive variant="dark">
            <thead>
              <tr>
                <th>Game</th>
                <th>User</th>
                <th>Type</th>
                <th>Amount</th>
                <th>Multiplier</th>
                <th>Result</th>
                <th>Payout (BSV)</th>
              </tr>
            </thead>

            <tbody>
              {gamesBets &&
                gamesBets.length > 0 &&
                gamesBets.map((el, i) => (
                  <>
                    <>
                      <tr
                        onClick={() => handleShow(el)}
                        key={`gamesBetsTr-${Date.now() + i}`}
                      >
                        <td>{el?.gameType ? el.gameType : "---"} </td>
                        <td>
                       <img src={usernew} alt="user" />   {el?.userid?.username ? el?.userid?.username : "---"}
                        </td>
                        <td>{el?.betType ? el.betType : "---"} </td>
                        <td>  <img src={money} alt="user" />  ${el?.betAmount ? el.betAmount : "0"} </td>
                        <td>{el?.multiplier ? `${el.multiplier}X` : "---"} </td>
                        <td> {el?.result ? el.result : "---"} </td>
                        <td
                          className={`${
                            el?.profit_loss
                              ? el.profit_loss > 0
                                ? "text-success"
                                : "text-danger"
                              : ""
                          }`}
                        >
                          ${el?.profit_loss ? el.profit_loss.toFixed(2) : "0"}{" "}
                        </td>
                      </tr>
                    </>
                  </>
                ))}
            </tbody>
          </Table>
        </Tab>
        <Tab eventKey="WINNERS" title="WINNERS">
          <Table bordered responsive variant="dark">
            <thead>
              <tr>
                <th>#Nonce</th>
                <th>Seed Hash</th>
                <th>Result</th>
                <th>Verify</th>
              </tr>
            </thead>
            <tbody>
              {gameHistory &&
                gameHistory.map((el) => {
                  return (
                    <>
                      <tr>
                        <td>{el.nonce}</td>
                        <td>
                          {el.secret && el.secret.slice(0, 50)}...
                          <CopyToClipboard text={el.secret}>
                            <Button
                              className="copy-btn"
                              onClick={() => {
                                copyClipBoard();
                              }}
                            >
                              <i className="fa fa-copy"></i>
                            </Button>
                          </CopyToClipboard>
                        </td>
                        <td>
                          {el.result && el.result.number},
                          {el.result && el.result.color}
                        </td>
                        <td>
                          <Link to={`/rouletteresult/${el.secret}/${el.nonce}`}>
                            Verify
                          </Link>
                          {/* 
    <Link to="/rouletteresult">
    Verify</Link> */}
                        </td>
                      </tr>
                    </>
                  );
                })}
            </tbody>
          </Table>
        </Tab>
      </Tabs>
      <>
        <Result show={show} handleClose={handleClose} gameResult={gameResult} />
      </>
    </div>
  );
};

// BetHistory.propTypes = {
// 	curGameUsers: PropTypes.array.isRequired,
// 	curGamePlayerStatus: PropTypes.func.isRequired,
// 	currentGameTableField: PropTypes.array.isRequired,
// 	allGamesResult: PropTypes.array.isRequired,
// 	currentGamesHash: PropTypes.array.isRequired,
// };

export default BetHistory;
